@import "../../global.scss";

.works {
	background-color: #eeeeee;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	@include mobile {
		overflow: hidden;
	}
	h1 {
		font-size: 50px;
		position: absolute;
		padding: 20px;
		top: 10px;
		@include mobile {
			padding-bottom: 5px;
			font-size: 25px;
		}
	}
	.arrow {
		height: 60px;
		position: absolute;
		transition: all 0.5s ease;
		@include mobile {
			display: none;
		}

		&.left {
			left: 100px;
			transform: rotateY(180deg);
			color: white;
		}
		&.right {
			right: 100px;
		}
		&:hover {
			height: 70px;
		}
	}

	.slider {
		height: 350px;
		display: flex;
		position: absolute;
		left: 0;
		transition: all 1s ease;
		@include mobile {
			height: 100vh;
			flex-direction: column;
			justify-content: center;
		}

		.container {
			width: 100vw;
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;

			.item {
				width: 700px;
				height: 100%;
				background-color: white;
				border-radius: 20px;
				display: flex;
				align-items: center;
				justify-content: center;
				transition: all 1.5s ease;
				@include mobile {
					width: 90%;
					height: 180px;
					margin: 5px 0;
				}
				&:hover {
					// margin: 0 35px;
					overflow: hidden;
					border-radius: 20%;
					transform: scale(1.1);
					@include mobile {
						border-radius: 10%;
						transform: none;
					}
				}
				.left {
					flex: 6;
					height: 80%;
					display: flex;
					align-items: center;
					justify-content: center;
					@include mobile {
						height: 90%;
						transform: none;
					}
					.leftContainer {
						width: 90%;
						height: 70%;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						@include mobile {
							height: 90%;
						}
						.imgContainer {
							width: 40px;
							height: 40px;
							background-color: rgb(245, 244, 225);
							border-radius: 30%;
							display: flex;
							align-items: center;
							justify-content: center;
							@include mobile {
								display: none;
							}
							img {
								width: 30px;
								@include mobile {
									display: none;
								}
							}
						}
						h2 {
							font-size: 20px;
							@include mobile {
								font-size: 10px;
							}
						}
						p {
							font-size: 13px;
							@include mobile {
								font-size: 10px;
							}
						}
						span {
							font-size: 12px;
							font-weight: 600;
							text-decoration: underline;
							cursor: pointer;
							@include mobile {
								font-size: 10px;
							}
						}
					}
				}
				.right {
					flex: 8;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					overflow: hidden;
					@include mobile {
						flex: 3;
						width: 200px;
					}
					img {
						width: 400px;
						transform: rotate(-10deg);
						@include mobile {
							width: 200px;
						}
					}
				}
			}
		}
	}
}
