@import "../../global.scss";

.portfolio {
	background-color: white;
	display: flex;
	flex-direction: column;
	align-items: center;

	h1 {
		font-size: 50px;
		@include mobile {
			font-size: 30px;
		}
	}
	ul {
		margin: 10px;
		padding: 0;
		list-style: none;
		display: flex;
		@include mobile {
			margin: 15px 0px;
			flex-wrap: wrap;
			justify-content: center;
		}
	}
	.container {
		width: 70%;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		@include mobile {
			width: 100%;
		}

		.item {
			width: 400px;
			height: 250px;
			border-radius: 20px;
			border: 1px solid rgb(240, 239, 239);
			margin: 10px 20px;
			display: flex;
			align-items: center;
			justify-content: center;
			color: white;
			position: relative;
			transition: all 1s ease;
			cursor: pointer;
			@include mobile {
				width: 150px;
				height: 125px;
				border-radius: 10px;
				border: 1px solid rgb(240, 239, 239);
				margin: 10px 10px;
				flex-direction: row;
				align-items: center;
				justify-content: center;
			}

			h3 {
				position: absolute;
				font-size: 20px;
				@include mobile {
					font-size: 10px;
					// margin: auto 20px;
				}
			}
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				z-index: 1;
			}
			&:hover {
				background-color: $mainColor;
				img {
					opacity: 0.2;
					z-index: 0;
				}
			}
		}
	}
}
