@import "../../global.scss";

.contact {
	display: flex;
	background-color: white;

	.left {
		flex: 2;
		align-items: center;
		justify-content: center;
		overflow: hidden;
		@include mobile {
			display: none;
		}
		img {
			height: 100%;
		}
	}

	.right {
		flex: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-evenly;

		.form {
			width: 80%;
			height: 70%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin: 50px;
			margin-right: 60px;
			padding: 40px;
			border-radius: 30px;
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
			@include mobile {
				margin: 0;
				// margin-right: 60px;
				padding: 25px;
			}
			h2 {
				font-size: 35px;
				font-weight: bold;
				margin-bottom: 40px;
				text-transform: uppercase;
				overflow: hidden;
				@include mobile {
					margin: 0px;
					padding-bottom: 50px;
				}
			}
			input {
				border: none;
				border-radius: 15px;
				padding: 12px 20px;
				margin-bottom: 20px;
				width: 80%;
				font-size: 20px;
				background-color: #fcfcfc;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

				&:focus {
					outline: none;
					box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
				}
			}
			textarea {
				width: 80%;
				height: 180px;
				background-color: #fafafa;
				border: none;
				border-radius: 15px;
				padding: 12px 20px;
				font-size: 20px;
				margin-bottom: 15px;
				box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

				&:focus {
					outline: none;
					box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
				}
			}

			.form-submit-btn {
				background-color: #007bff;
				border: none;
				border-radius: 5px;
				color: #fff;
				cursor: pointer;
				font-size: 16px;
				padding: 12px 40px;
				text-transform: uppercase;
				overflow: hidden;
				&:hover {
					box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
					background-color: #0062cc;
				}
			}
			span {
				overflow: hidden;
				padding: 10px;
				color: green;
			}
		}
	}
}
