@import "../../global.scss";

.intro {
	background-color: white;
	display: flex;
	@include mobile {
		flex-direction: column;
		align-items: center;
	}
	.left {
		flex: 0.5;
		overflow: hidden;
		.imgContainer {
			width: 900px;
			height: 900px;
			background-color: crimson;
			border-radius: 50%;
			display: flex;
			align-items: flex-end;
			justify-content: center;
			float: right;
			@include mobile {
				width: 400px;
				height: 600px;
				border-radius: 50%;

				align-items: flex-start;
			}
			img {
				width: 90%;
				@include mobile {
					height: 60%;
				}
			}
		}
	}
	.right {
		flex: 0.5;
		position: relative;
		.wrapper {
			width: 100%;
			height: 100%;
			padding-left: 50px;
			margin-left: 50px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			@include mobile {
				padding-left: 0;
				margin-left: 0;
			}
			h1 {
				font-size: 100px;
				margin: 10px 0;
				@include mobile {
					font-size: 50px;
				}
			}
			h2 {
				font-size: 45px;
				@include mobile {
					font-size: 25px;
				}
			}
			h3 {
				font-size: 40px;
				@include mobile {
					font-size: 25px;
				}
				span {
					font-size: inherit;
					color: crimson;
				}
				.ityped-cursor {
					animation: blink 1s infinite;
					animation-direction: alternate;
				}

				@keyframes blink {
					50% {
						opacity: 1;
					}
					100% {
						opacity: 0;
					}
				}
			}
		}
		a {
			position: absolute;
			bottom: 10px;
			left: 40%;

			img {
				width: 40px;
				animation: arrowBlink 2s infinite;
			}
			@keyframes arrowBlink {
				100% {
					opacity: 0;
				}
			}
		}
	}
}
