@import "../../global.scss";

.testimonials {
	background-color: white;
	display: flex;
	flex-direction: column;
	align-items: center;

	h1 {
		font-size: 50px;
		@include mobile {
			padding-bottom: 5px;
			font-size: 25px;
		}
	}
	transition: transform all 1s ease;

	.container {
		background-color: white;
		width: 100%;
		height: 80%;
		display: flex;
		align-items: center;
		justify-content: center;
		@include mobile {
			flex-wrap: wrap;
			// overflow: hidden;
		}

		.card {
			width: 300px;
			height: 70%;
			border-radius: 10px;
			box-shadow: 0px 0px 15px -8px black;
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			padding: 20px;
			transition: all 1s ease;
			@include mobile {
				height: 180px;
				// padding: 10px;
				margin: 5px 0;
			}
			&.featured {
				width: 350px;
				height: 80%;
				margin: 0 30px;
				@include mobile {
					width: 300px;
					height: 180px;
					margin: 0 10px;
				}
			}
			&:hover {
				margin: 0 35px;

				transform: scale(1.1);
				@include mobile {
					margin: 0;

					transform: none;
				}
			}

			.top {
				display: flex;
				align-items: center;
				justify-content: center;
				overflow: hidden;
			}
			img {
				transition: all 3s ease;

				&.left,
				&.right {
					height: 35px;
					@include mobile {
						height: 20px;
					}
				}
				&.user {
					// width: 100%;
					// height: 100%;
					height: 70px;
					width: 70px;
					border-radius: 50%;
					object-fit: cover;
					margin: 0 30px;
					transition: all 2s ease;
					@include mobile {
						height: 40px;
						width: 40px;
						border-radius: 50%;
						margin: 10px 20px;
					}
					&:hover {
						position: absolute;
						transform: scale(1.8); /* Increase the size by 10% */
						z-index: 0; /* Bring the image to the front */
						box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
						@include mobile {
							position: relative;
							transform: none;
						}
					}
				}
			}
		}
		.center {
			padding: 10px;
			border-radius: 10px;
			background-color: rgb(237, 237, 237);
			@include mobile {
				height: 50px;
				padding: 10px;
				border-radius: 5px;
			}
		}

		.bottom {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			@include mobile {
				flex-direction: row;
				justify-content: space-evenly;
				// flex-wrap: wrap;
			}
			h3 {
				@include mobile {
					// margin-bottom: 10px;
				}
			}
			h4 {
				color: gray;
				@include mobile {
					font-size: 10px;
				}
			}
		}
	}
}
