@import "../../global.scss";

li {
	font-size: 20px;
	margin-right: 50px;
	padding: 7px;
	border-radius: 15px;
	cursor: pointer;
	@include mobile {
		margin-right: 20px;
		justify-content: center;
	}

	&.active {
		background-color: $mainColor;
		color: white;
	}
	&:hover {
		color: black;
		background-color: lightgray;
	}
}
